import React from "react";
import { Card } from "./components/Card/Card";
import Delete from "./images/delete.png";
import Setting from "./images/settings.png";
import NewTeam from "./images/NewTeam.png";
import { useHistory } from "react-router-dom";
import { useQuery } from "react-query";
import { axios, getToken } from "./utils/api";
import { Team } from "./utils/types";

export default function Teams() {
  const history = useHistory();
  const query = useQuery("teams", async () => {
    const data = await axios.post(
      "/api/teams",
      {},
      {
        headers: { authorization: "Bearer " + getToken() },
      }
    );
    return data.data as Team[];
  });
  return (
    <div style={{ padding: "2em 1em" }}>
      <div
        style={{ backgroundColor: "white", borderRadius: 10, padding: "2em" }}
      >
        <div>TEAMS</div>
        <div style={{ marginTop: "1em" }}>
          <Card>
            <Card.Body background="gradient" style={{ borderRadius: 8 }}>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  padding: "0 2em",
                }}
              >
                <div>Name</div>
                <div>Email</div>
                <div>Action</div>
              </div>
            </Card.Body>
          </Card>
          {query.data?.map((team) => {
            return (
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  padding: "0 2em",
                  backgroundColor: "#EFF3F4",
                  color: "black",
                  marginTop: "1em",
                  borderRadius: 8,
                }}
                key={team.id}
              >
                <div style={{ padding: "0.5em 0" }}>{team.name}</div>
                <div
                  style={{
                    padding: "0 0.5em",
                    backgroundColor: "#B54679",
                    borderRadius: 4,
                    color: "white",
                    fontSize: "1.5em",
                    textAlign: "center",
                  }}
                >
                  {team.numUsers}
                </div>
                <div style={{ padding: "0.5em 0" }}>
                  <img
                    src={Setting}
                    alt="Setting"
                    style={{
                      objectFit: "contain",
                      maxHeight: 25,
                      borderRadius: "50%",
                    }}
                  />
                  <img
                    src={Delete}
                    alt="Delete"
                    style={{
                      objectFit: "contain",
                      maxHeight: 25,
                      borderRadius: "50%",
                    }}
                  />
                </div>
              </div>
            );
          })}
        </div>
      </div>
      <div
        style={{ width: "100%", textAlign: "right" }}
        onClick={() => {
          history.push("/teams/new");
        }}
      >
        <img
          src={NewTeam}
          alt="New Team"
          style={{ maxHeight: 50, marginRight: 50 }}
        />
      </div>
    </div>
  );
}
