import React from "react";
import { useHistory } from "react-router-dom";
import { Card } from "./components/Card/Card";
import Setting from "./images/settings.png";
import Delete from "./images/delete.png";
import NewTeam from "./images/NewTeam.png";
import { useQuery } from "react-query";
import { axios, getToken } from "./utils/api";
import { BasicUser } from "./utils/types";

export default function Users() {
  const history = useHistory();
  const query = useQuery("users", () => {
    return axios
      .post(
        "/api/admin/user/get-company",
        {},
        {
          headers: { authorization: "Bearer " + getToken() },
        }
      )
      .then((data) => data.data as BasicUser[]);
  });
  return (
    <div style={{ padding: "2em 1em" }}>
      <div
        style={{ backgroundColor: "white", borderRadius: 10, padding: "2em" }}
      >
        <div>USERS</div>
        <div style={{ marginTop: "1em" }}>
          <Card>
            <Card.Body background="gradient" style={{ borderRadius: 8 }}>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  padding: "0 2em",
                }}
              >
                <div>Name</div>
                <div>Email</div>
                <div>Forward To Device</div>
                <div>Availability</div>
                <div>Number</div>
                <div>Status</div>
                <div>Edit / Delete</div>
              </div>
            </Card.Body>
          </Card>
          {query.data?.map((user) => {
            return (
              <div
                key={user.id}
                onClick={() => history.push("/users/" + user.id)}
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                  padding: "0 2em",
                  backgroundColor: "#EFF3F4",
                  color: "black",
                  marginTop: "1em",
                  borderRadius: 8,
                }}
              >
                <div
                  style={{
                    padding: "0.5em 0",
                    width: "14.28%",
                    overflow: "hidden",
                  }}
                >{`${user.firstName} ${user.lastName ?? ""}`}</div>
                <div
                  style={{
                    padding: "0.5em 0",
                    width: "12%",
                    overflow: "hidden",
                  }}
                >
                  {user.email}
                </div>
                <div style={{ padding: "0.5em 0", width: "14.28%" }}>
                  Enabled
                </div>
                <div style={{ padding: "0.5em 0", width: "14.28%" }}>
                  Available
                </div>
                <div style={{ padding: "0.5em 0", width: "14.28%" }}>
                  {user.phoneNumbers.length}
                </div>
                <div style={{ padding: "0.5em 0", width: "14.28%" }}>
                  Active
                </div>
                <div style={{ padding: "0.5em 0" }}>
                  <img
                    src={Setting}
                    alt="Setting"
                    style={{
                      objectFit: "contain",
                      maxHeight: 25,
                      borderRadius: "50%",
                    }}
                  />
                  <img
                    src={Delete}
                    alt="Delete"
                    style={{
                      objectFit: "contain",
                      maxHeight: 25,
                      borderRadius: "50%",
                    }}
                  />
                </div>
              </div>
            );
          })}
        </div>
      </div>
      <div
        style={{ width: "100%", textAlign: "right" }}
        onClick={() => {
          history.push("/users/new");
        }}
      >
        <img
          src={NewTeam}
          alt="New Team"
          style={{ maxHeight: 50, marginRight: 50 }}
        />
      </div>
    </div>
  );
}
